.portada1{
    background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
    padding: 40px;
    display: flex;
    justify-content: center;
}

.flex1 {
    display: flex;
    flex-direction: column;
    margin-top: 60px;
    align-items: center;
    justify-content: space-evenly;
  }

.imagen5{
    width: 380px;
    height: 380px;
    margin-top: 50px;
}

.titulo {
    text-align: center;
    color: #000;
    margin-top: -120px;
}
.parafo {
    text-align: center;
    margin-top: -150px;
    font-size: 20px;
    color: black;
    padding-left: 40px;
}
/* ----------------------------Media Querys--------------------- */

@media screen and (max-width: 1100px){
.titulo{
margin-top: -80px;
}    
.parafo{
 margin-top: -100px;
}
    }


@media screen and (max-width: 991px){
    .imagen5{
   margin-left: 0;
   display: flex;
   flex-direction: column;


}
    .titulo{
      margin-top: 10px;
      margin-right: -190px;
      margin-left: -150px;
    }
    .parafo{
     margin-top: -30px;
    }
}


@media screen and (max-width:757px){
    .portada1{
        display: flex;
        flex-direction: column;
       
    }
    .imagen5{
        display: block;
        margin: auto;
        margin-top: 40px;
    }
    .titulo{
        margin-top: -50px;
    }
    .parafo{
        margin-top: 20px;
    }
}
@media screen and (max-width:575px){
    .parafo{
        padding-left: 0px;
    }
    .imagen5{
        max-width: 100%;
        height: auto;
    }
}



