.principal {
    text-align: center;
    padding: 100px;
    margin-top: -40px;
    color: #140b0b;
}
.prueba5{
    margin-top: 40px;
    font-size: 20px;
}
.padre{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.projects p{
    margin-top: 40px;
    text-align: center;
    font-size: 20px;
}

.contenedor {
    position: relative;
    border-radius: 10px;
    margin: 25px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid #5DADE2;
    background-color:#F7F9F9;
    height: 360px;
    width: 260px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
    transition: all .3s;
}

.cuadro {
    position: relative;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 2px solid #5DADE2;
    background-color: #F7F9F9;
    height: 360px;
    width: 260px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
    transition: all .3s;
}

.cuadro img {
    border-radius: 10px;
    width: 100%;
    height: 60%;
}

.cuadro a{
    position: absolute;
    top: 360px;
    margin: 0 2rem;
    left: 60px;
    text-decoration: none;
    padding: 10px;
    background-color: #000;
    color: #F7F9F9;
    border-radius: 20px;
    font-size: 20px;
    transition: all .5s;
}










